import React, { useState } from "react";
import { useSelector } from "react-redux";
import { makeStyles } from "@material-ui/core/styles";
import {
  AppBar,
  Grid,
  Box,
  Card,
  CardHeader,
  CardContent,
  Paper
} from "@material-ui/core";
import classNames from "classnames";
import { MDBBtn, MDBIcon } from "mdbreact";
import MiniCart from "./MiniCart";

export default function Carrinho() {
  const [hideCardBody, setHideCardBody] = useState(true);
  const cart = useSelector(state => state.cart);
  const useStyles = makeStyles(theme => ({
    appBar: {
      top: "auto",
      bottom: 56
    },
    cartclass: {
      color: "black"
    },
    hideCardBodyclass: {
      display: hideCardBody ? "none" : "block"
    }
  }));
  const classes = useStyles();

  function toggleCardBody() {
    setHideCardBody(!hideCardBody);
  }

  function getFooter() {
    if (cart.items.length !== 0) {
      return (
        <Box>
          <MDBBtn color="success" className="w-100">
            Finalizar Compra
          </MDBBtn>
        </Box>
      );
    }
  }

  return (
    <AppBar color="primary" className={classes.appBar}>
      <Box>
        <Card style={{ overflow: "auto" }}>
          <CardHeader
            onClick={toggleCardBody}
            className="success-color text-white"
            avatar={
              <div className="d-flex flex-row">
                <div className="flex-grow-1 ">
                  <strong>Seu carrinho</strong>
                </div>
                <div className="border-left border-white text-right px-2">
                  <MiniCart />
                </div>
                <div className="text-right">R$ {cart.total}</div>
              </div>
            }
          />

          <CardContent
            className={classNames(classes.CartClass, classes.hideCardBodyclass)}
            style={{ maxHeight: "40vh" }}
          >
            <Paper elevation={3} className="p-2">
              {cart.items.length === 0 ? <h5>Vazio</h5> : ""}
              {cart.items.map(item => (
                <Grid container key={item.id}>
                  <Grid item xs={8} className="text-left">
                    {item.name}
                  </Grid>
                  <Grid item xs={2} className="text-center">
                    <MDBIcon icon="minus-circle" /> 1
                    <MDBIcon icon="plus-circle" />
                  </Grid>
                  <Grid item xs={2} className="text-right">
                    {item.price}
                  </Grid>
                </Grid>
              ))}
            </Paper>
            {getFooter()}
          </CardContent>
        </Card>
      </Box>
    </AppBar>
  );
}
